import React from 'react';
import Cardgaleri from './Cardgaleri2';


const Cardlist = ({data})=>{
    return(
        data.map((user, i) => {
            return (
                <Cardgaleri
                    key={i}
                    title={data[i].title}
                    desc={data[i].desc}
                    coverurl={data[i].coverurl}
                />
            );
        })
    )
}

export default Cardlist;